#lodingWrap {
  display: table;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #111 url(../assets/img/darkNoise.jpg);
  background: url(../assets/img/shadedBorders.png) left top/100% 100%,
    #111 url(../assets/img/darkNoise.jpg);
  text-align: center;
  z-index: 1000000000;
  line-height: 150%;
  font-size: 20px;
}

#loader {
  position: relative;
  padding: 32px;
  animation-name: appear;
  animation-iteration-count: 1;
  animation-timing-function: ease-out;
  animation-duration: 0.5s;
  padding-top: 110px;
  top: 40%;
}

@keyframes appear {
  from {
    opacity: 0;
    top: 0px;
  }
  to {
    opacity: 1;
    top: 40%;
  }
}
#loading {
  text-shadow: 0px 0px 3px rgba(255, 255, 255, 0.5), 0px 0px 20px #39f;
  animation-name: blink;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-duration: 0.75s;
}
.blinking {
  animation-name: blink;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  animation-duration: 0.75s;
}
@keyframes blink {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}
.spinny {
  display: block;
  width: 100px;
  height: 100px;
  position: absolute;
  left: 50%;
  top: 0px;
  margin-left: -50px;
  animation-name: loadSpin;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}
.spinny {
  background: url(../assets/img/cookie-icon.png);
  background-size: 99px 99px;
  animation-duration: 5s;
}

@keyframes loadSpin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
