/*=====================================================================================
SECTIONS
=======================================================================================*/
.section {
  font-variant: small-caps;
  text-align: center;
  padding: 0px 16px;
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0)
  );
  margin: 12px 0px;
  text-shadow: 0px -2px 6px rgba(255, 255, 200, 0.6),
    0px 1px 0px rgba(100, 100, 100, 1), 0px 2px 4px rgba(0, 0, 0, 1);
}
.section:after {
  content: "";
  display: block;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0.25),
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0)
  );
  height: 1px;
  margin: 6px 0px;
}
.section:before {
  content: "";
  display: block;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0.25),
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 0)
  );
  height: 1px;
  margin: 6px 0px;
}
.subsection {
  padding: 8px 0px;
  font-size: 14px;
}
.subsection div.title {
  font-size: 22px;
  padding-left: 16px;
  margin-bottom: 8px;
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0)
  );
}
.subsection div.title:after {
  content: "";
  display: block;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0.25),
    rgba(255, 255, 255, 0)
  );
  height: 1px;
  width: 50%;
  margin: 6px 0px;
  margin-left: -16px;
}
.subsection div.title:before {
  content: "";
  display: block;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0.25),
    rgba(255, 255, 255, 0)
  );
  height: 1px;
  width: 50%;
  margin: 6px 0px;
  margin-left: -16px;
}
