#ascendNumber {
  display: none;
  position: absolute;
  right: 115px;
  top: 22px;
  font-size: 14px;
  font-weight: bold;
  font-family: Georgia;
  color: #fff;
  text-shadow: 0px -1px 1px #09f, 0px 1px 1px #f04;
}
#legacyButton:hover > #ascendNumber {
  text-shadow: 0px -1px 1px #09f, 0px 1px 1px #f04, 0px -1px 1px #09f,
    0px 1px 1px #f04, 0px 0px 4px #fff;
}

#ascendTooltip {
  display: none;
  position: absolute;
  right: 0px;
  top: 52px;
  font-family: Tahoma, Arial, sans serif;
  font-size: 11px;
  width: 220px;
