#lumps {
  display: none;
  position: absolute;
  width: 32px;
  height: 32px;
  left: -8px;
  bottom: -12px;
  z-index: 10000;
  cursor: pointer;
  filter: drop-shadow(0px 3px 2px #000);
  -webkit-filter: drop-shadow(0px 3px 2px #000);
}
#lumps:hover #lumpsIcon,
#lumps:hover #lumpsIcon2 {
  top: -10px;
}
#lumpsIcon,
#lumpsIcon2 {
  width: 48px;
  height: 48px;
  position: absolute;
  left: -8px;
  top: -8px;
  pointer-events: none;
}
#lumpsAmount {
  font-size: 12px;
  color: #6cf;
  position: absolute;
  left: 36px;
  top: 6px;
  pointer-events: none;
  text-align: left;
  width: 200px;
}
.lumpsOn #lumps {
  display: block;
}

.lumpRefill {
  cursor: pointer;
  width: 48px;
  height: 48px;
  position: absolute;
  left: 0px;
  top: 0px;
  transform: scale(0.5);
  z-index: 1000;
  transition: transform 0.05s;
}
.lumpRefill:hover {
  transform: scale(1);
}
.lumpRefill:active {
  transform: scale(0.4);
}
body {
  scrollbar-color: #bbb #111;
}

/*show scrollbars in safari*/
::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 17px;
}
::-webkit-scrollbar-track {
  background-color: #111;
}
::-webkit-scrollbar-thumb {
  background-color: #bbb;
  border: 2px solid #111;
  box-shadow: 0px 0px 0px 1px #eee inset;
}

.lanking-btn {
  position: absolute;
  right: 20px;
  cursor: pointer;
  padding: 1px;
  transition: all 1s ease-out;
  border-radius: 50%;
  display: none;
  background-color: #4e3b3960;
}

.lanking-btn:hover {
  /* box-shadow: #000; */
  background-color: #a89998;
}

@media only screen and (max-width: 500px) {
  .scene {
    position: absolute;
    left: 50%;
    top: 0px;
    bottom: 0px;
    width: 100%;
    /* min-width: 390px; */
    transform: translateX(-50%);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .panelButton {
    width: 96%;
  }

  .leader-board-wrapper {
    width: 100% !important;
  }
}

/**{overflow:hidden!important;}*/
@media only screen and (max-width: 1200px) {
  .leader-board-wrapper {
    padding-top: 100px;
    opacity: 0;
    position: absolute;
    transform: translateX(100%);
    top: 0px;
    width: 35%;
    min-width: 390px;
    z-index: 1;
    height: 100%;
    transition: all 0.3s ease-out;
  }

  .show-data {
    opacity: 1;
    left: 50%;
    top: 0px;
    transform: translateX(-50%);
    background-color: #080808e1;
    z-index: 99;
  }

  .lanking-btn {
    position: absolute;
    right: 20px;
    cursor: pointer;
    width: 35px;
    height: 35px;
    padding: 1px;
    transition: all 1s ease-out;
    border-radius: 50%;
    display: block !important;
  }
}

.toast-content {
  color: #000;
  font-size: 20px;
}

.tooltip {
  position: relative;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: auto;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 3px;
  position: absolute;
  bottom: 100%;
  left: 50%;
  opacity: 0;
  transition: opacity 0.3s;
  transform: translateX(-50%);
  z-index: 1000;
}

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

